* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 0;
}

.country-item {
  display: flex;
  align-items: center;

  font-weight: 700;
  margin: 0;
  padding: 0;
}

.country-item:not(:first-child) {
  margin-top: 10px;
}

.country-icon {
  margin-right: 20px;
}

.title__wrapper {
  display: flex;
  align-items: center;
}

.title {
  font-size: 30px;
  font-weight: 700;
}

.country-description {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-description__item:not(:first-child) {
  margin-top: 15px;
}

.country-description__text {
  margin-right: 10px;
  font-weight: 700;
}
